<template>
    <div v-show="editMode" class="container">
        <input
            v-model="localNote.title"
            ref="titleInput"
            :class="titleClass"
            class="form-control"
            placeholder="Title"
            type="text"
            @input="saveItem"
            @focusout="validateTitle"
        />
        <textarea
            ref="textInput"
            class="form-control p-2"
            placeholder="Note"
            :disabled="!titleIsValid"
            rows="1"
            v-model="localNote.text"
            @input="autoResize">
        </textarea>
    </div>
    <div v-show="!editMode" class="container">
        <span class="badge text-bg-info">Created: {{ new Date(localNote.id).toLocaleString(userLocale) }}</span>
        <p :class="titleClass"
            class="text-muted pt-2"
            v-text="localNote.title"
        />
        <hr>
        <div v-html="parsedText"></div>
    </div>
    <BaseModal modalId="confirmDeleteModal" title="Are you sure you want to delete this note?">
        <template #footer>
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">
                <i class="bi bi-x-lg"></i> Cancel
            </button>
            <button @click="deleteItem" type="button" class="btn btn-danger" data-bs-dismiss="modal">
                <i class="bi bi-trash3"></i> Delete
            </button>
        </template>
    </BaseModal>
    <FixedBottom>
        <button @click="editMode = !editMode" :style="buttonStyle" :class="buttonClass" class="btn-primary" type="button">
            <Transition mode="in-out">
                <i v-if="editMode" class="bi bi-eye-fill"></i>
                <i v-else class="bi bi-pencil-fill  "></i>
            </Transition>
        </button>
        <button @click="hideEditor" :style="buttonStyle" :class="buttonClass" class="ms-2 btn-danger" type="button">
                <i class="bi bi-x-lg"></i>
        </button>
        <button :disabled="!deleteEnabled" data-bs-toggle="modal" data-bs-target="#confirmDeleteModal" :style="buttonStyle" :class="buttonClass" class="ms-2 btn-danger" type="button">
                <i class="bi bi-trash3"></i>
        </button>
    </FixedBottom>
</template>
<script setup>
import { ref, watch, useTemplateRef, nextTick, defineModel, computed, defineProps, defineEmits, onMounted } from 'vue'
import FixedBottom from './FixedBottom.vue'
import BaseModal from './BaseModal.vue';
let marked = require('marked')
marked.use({
    "breaks":true,"silent":true
})

const model = defineModel()
const props = defineProps({
    isNew:{type:Boolean,required:true}
})
const emit = defineEmits(['hide-editor','update','delete']);

const editMode = ref(props.isNew);
const textInput = useTemplateRef("textInput");
const titleInput = useTemplateRef("titleInput");
const localNote = ref({...model.value});
const lastValidTitle = ref(null);

const parsedText = computed(()=>{
    return marked.parse(localNote.value.text);
})

const deleteEnabled = computed(()=>{
    return titleIsValid.value || (!props.isNew);
})

const titleIsValid = computed(()=>{
    return localNote.value.title.trim().length >= 3;
})

const validateTitle = () => {
    if (!titleIsValid.value) {
        localNote.value.title = lastValidTitle.value || "";
    }
}

const hideEditor = () => {
    emit("hide-editor");
}

const saveItem = () => {
    if(titleIsValid.value){
        emit('update', localNote.value);
    }
};

const deleteItem = () => {
    emit('delete', localNote.value.id);
    emit('hide-editor');
};

function autoResize() {
    if(textInput.value) {
        textInput.value.style.height = "auto";
        textInput.value.style.height = textInput.value.scrollHeight + 'px';
    }
    saveItem();
}

const addClasses = () => {
    const quotes = document.getElementsByTagName("blockquote");
    for (const q of quotes) {
        q.classList.add("blockquote", "border-start","border-dark-subtle", "border-3","ps-2","py-1","fs-6");
    }
}

async function setFocus() {
    await nextTick()
    autoResize()
    if (localNote.value.title !== ""){
        textInput.value.focus();
    } else {
        titleInput.value.focus();
    }
}

watch(editMode,(newValue)=>{
    if (newValue) {
        setFocus();
    } else {
        addClasses();
    }
},{ immediate: true })

watch(
    () => localNote.value.title,
    (newValue) => {
        if(titleIsValid.value){
            lastValidTitle.value = newValue;
        }
    },{immediate:true}
)

onMounted(autoResize);
onMounted(addClasses);
</script>
<script>
const userLocale = navigator.language || navigator.languages[0];
const titleClass = "fs-3 mb-2";
const buttonClass = "btn rounded-circle fs-5 shadow"
const buttonStyle = {
    height:"50px",
    width:"50px"
}
</script>
<style>
.btn {
    overflow-y: hidden;
}
textarea {
    overflow: hidden; /* disable scrolling due to auto resize */
}
</style>